<template>
		<div class="container-fluid  p-0 main">
		<cust-header variant="Кашигин С.С." group="P3210" name="1055"></cust-header>
		<div class="row my-row">
			<div class="col my-col">
				<h1>Registration page</h1>
				<label id="errorLabel"></label>
			</div>
		</div>
		<div>
			<form id="form-submit" action="rest/user/signup" @submit.prevent="regValid(username, password, repeatPassword)" method="post">
				<div class="row my-row">
					<div class="col my-col">
						<label for="inputLogin" >Логин: </label>
					</div>
				</div>
				<div class="row my-row">
					<div class="col my-col">
						<input id="inputLogin" name="login" type="text" v-model="username" placeholder="Придумайте логин" />
					</div>
				</div>
				<div class="row my-row">
					<div class="col my-col">
						<label for="inputPassword">Пароль: </label>
					</div>
				</div>
				<div class="row my-row">
					<div class="col my-col">
						<input id="inputPassword" name="password" type="password" v-model="password" placeholder="Придумайте пароль" />
					</div>
				</div>
				<div class="row my-row">
					<div class="col my-col">
						<label for="repeatPassword">Повторите пароль:</label>
					</div>
				</div>
				<div class="row my-row">
					<div class="col my-col">
						<input id="repeatPassword" name="repeatPassword" type="password" v-model="repeatPassword" placeholder="Повторите пароль" />
					</div>
				</div>
				<div class="row my-row">
					<div class="col my-col">
						<button type="submit">Зарегистрироваться</button>
					</div>
					<div class="col my-col">
						<a href="login.html">Войти</a>
					</div>
				</div>
			</form>
		</div>
	</div>
</template>


<script>
import CustHeader from './Header.vue'
import $ from 'jquery';

	export default{
		data() {
			return {
				username: "",
				password: "",
				repeatPassword: ""
			}
		},
		components: {
			CustHeader,
		},
		methods: {
			signup(username, password) {
					return $.post(
					'/TaskListManager/rest/user/signup',
					{
						login: username,
						password: password,
					})
			},
			checkExistance(username) {
				return $.ajax({
					type: 'POST',
					url: '/TaskListManager/rest/user/exist',
					data:{
						login: username,
					},
					async : false,
					success: function() {
						$('form#form-submit').submit();
					},
					error: function() {
						alert("User already exist!");
					},
				})
			},
			regValid(username, password, repeatPassword) {
				if (username === "") {
					alert("Пустое имя пользователя!");
					return false;
				}
				if (password !== repeatPassword) {
					alert("Пароли не совпадают!");
					return false;
				}
				this.checkExistance(username);
			},
		},
	};
</script>

<style lang="scss">

@import "../scss/custom.scss";

/*
