		border: 3px dotted blue;
	}	
	.my-row {
		border: 3px solid red;
 }
*/
	.point-list {
		overflow-y: scroll;
		border: 1px solid;
	}

	.row :not(.header) {
		margin-top: 5px;
		margin-bottom: 5px;
	}

	button, input {
		margin: 2px;
		margin-bottom: 2px;
		min-width: 50px;
	}
	.header {
		background-color : orange;
		font-size : 18pt;
	}
	@media (max-width: 800px) {
		html {
			width: all;
		}
		body {
			position: fixed;
			width: 100%
		}
		button {
			margin: 0px;
			margin-bottom: 0px;
		}
		.container-fluid{
			width: 100%;
			vertical-align: top;
		}
		.main {
			width: 100%;
		}
		.header {
			width: 100%;
		}
		.container-fluid{
			max-width: 100%;
		}
		.row :not(.header) {
			margin-top: 1px;
			margin-bottom: 1px;
		}
	}
	@media (min-width: 800px) {
		.container-fluid{
			max-width: 800px;
			padding: 0px;
		}
		.main{
			min-width : 800px;
			min-height: 300px;
			display : inline-block;  
			background-color : white;
		}
		body {
			background-image: url("/TaskListManager/resources/img/back.png");
		}
		.main {
			border: 4px double black;
			border-top-right-radius : 14px;
			border-top-left-radius : 14px;
		}
		.header {
			border-top-right-radius : 11px;
			border-top-left-radius : 11px;
		}
 }

</style>